<template>
  <VanPopup
    v-model="show"
    round
    :close-on-popstate="closeOnPopstate"
    :close-on-click-overlay="closeOnclickOverlay"
    :safe-area-inset-bottom="safeAreaInsetBottom">
    <VanLoading size="50" />
  </VanPopup>
</template>

<script>
export default {
  data () {
    return {
      show: true,
      closeOnclickOverlay: false,
      closeOnPopstate: true,
      safeAreaInsetBottom: true
    }
  },
  async beforeCreate () {
    const { origin, pathname, search, hash } = location
    const phone = localStorage.getItem('giftBoxPhone')// 若用户已经购买过  则会存在localStorage
    try {
      await this.$apis.giftBox.checkOrder({
        phone: phone,
        product_sn: this.productSn
      })
      // 1000 留在当前页面  继续完成支付  不做任何操作
      location.replace(decodeURIComponent(`${origin}${pathname}${search}#/GiftBoxPay?${hash.split('?')[1]}`))
    } catch (err) {
      // 30001 学习过同类课程
      // 30002 学习过此课程
      // 30003 学习过此课程
      // 30004 学习过此课程
      // 33001 手机号未填写或未授权
      const { code, data: { order_num: orderNum } } = err
      if (code === 30006) {
        localStorage.setItem('giftBoxOrderNum', orderNum)// 存储order_num  在地址页面会用到
        this.$router.replace('/GiftBoxPaySuccess')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.van-popup {
  background-color: transparent !important;
  width: 100%;
  height: 100%;

  @include horVerCenter();
}
</style>
